<div class="project-content py-5"><div class="project-hero-image mb-5" :style="{ backgroundImage: 'url(' + project.backgroundImage + ')' }"></div><div class="columns is-mobile"><div class="column"><div class="project-header mb-5"><h2 class="is-size-4 has-text-weight-bold">{{ project.company }}</h2><div class="simple-divider"></div></div></div><div class="column"><div class="metric-block mb-5 has-text-right"><p class="heading">{{ project.projectType === 'main' ? 'Series' : 'Type' }}</p><p class="title">{{ project.series }}</p><p class="subtitle">{{ project.revenue }}</p></div></div></div><div class="project-overview mb-5"><h3 class="is-size-5 has-text-weight-bold mb-3">Overview</h3><div class="line-divider"></div><p class="mb-4">{{ project.overview }}</p><a class="link" :href="project.website" target="_blank" @click="trackWebsiteClick"><font-awesome-icon icon="link"></font-awesome-icon>&nbsp;Website</a></div><div class="project-component" v-if="project.projectType === 'main'"><component :is="project.companyComponent"></component></div></div>