<router-link class="link-block" v-if="project" :to="{ path: `/project/${project.id}` }" @click="handleClick"><div class="card project mb-5" :style="{ backgroundImage: 'url(' + project.backgroundImage + ')' }"><div class="has-text-centered" v-if="!project.backgroundImage"><div class="dots"><span class="dot1">&middot;</span><span class="dot2">&middot;</span><span class="dot3">&middot;</span></div></div></div><div class="levels card-description"><div class="level"><div class="level-left"><img class="logo is-hidden-desktop mb-4" :src="project.logo" alt="Logo" :title="project.company"><div class="is-size-6"> <span>{{ project.title }}</span></div></div><div class="level-right is-hidden-mobile"><img class="logo" :src="project.logo" alt="Logo" :title="project.company"></div></div></div></router-link>